
import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { SelectionModel } from '@angular/cdk/collections';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { DatePipe } from '@angular/common';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { ErrorService } from 'app/shared/services/error/error.service';
import { UtilityService } from 'app/shared/services/utility/utility.service';

export class ErrorModel {
  username: string;
  activity_name: string;
  application_platform: string;
  email: string;
  error_code: string;
  error_discription: string;
  project_code: string;
  project_name: string;
  url: string;
  created_date:string;
  convertDate:string;
}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list-error',
  templateUrl: './list-error.component.html',
  styleUrls: ['./list-error.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})
export class ListErrorComponent implements OnInit {
  filterEntity: ErrorModel;
  filterType: MatTableFilter;
  errorForm:FormGroup;
  currDate : any = new Date();
  displayedColumns: string[] = ['username', 'activity_name', 'application_platform', 'email', 'error_code', 'error_discription',
    'project_code', 'project_name','convertDate'];
  head = [['User Name ', 'Activity Name', 'Application Platform', 'Email', 'Error Code', 'Error Discription','Project Code','Project Name',
          'Date']];
  dataSource: MatTableDataSource<ErrorModel>;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  dialogRef: any;

  userNameSearch = false;
  activitySearch = false;
  applicationSearch = false;
  emailSearch = false;
  errorCodeSearch = false;
  errorDesiSearch = false;
  projCodeSearch = false;
  projNameSearch = false;
  urlSearch = false;
  convertDateSearch = false;
  menuArray:any;
  exportedData: any[];
  form: FormGroup;
  localStorage: any;
  tempDate = new Date();
  errorsLogs:any;
  constructor(private errorService: ErrorService, public dialog: MatDialog,private _utilityService:UtilityService,  
    private snackBar: MatSnackBar, private cdr: ChangeDetectorRef, public datepipe: DatePipe, private fb: FormBuilder,) {
    this.errorForm = this.fb.group({
      name:[''],
      toDate : [new Date()],
      fromDate : [new Date(this.tempDate.setDate(this.tempDate.getDate() - 2))]
    });
  }
  ngAfterViewInit(){
    this._utilityService.activeMenuItems$.subscribe(menuItemSelecte=> {
      this.menuArray = menuItemSelecte;
      if(this.menuArray == null || this.menuArray == undefined || this.menuArray.title == null || this.menuArray.title == undefined){
        this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
      }
      this.cdr.detectChanges();
    })
     
  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
   this.getErrorList();
    this.filterEntity = new ErrorModel();
    this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getErrorList() {
    
    this.errorService.getAllErrorLog().subscribe(
      (resp: any) => {
        //;
        if (resp != null) {
          this.errorsLogs = resp.data;
          for(var i =0;i<this.errorsLogs.length;i++){
            this.errorsLogs[i].convertDate = this.datepipe.transform(this.errorsLogs[i].created_date, 'dd/MM/yyyy hh:mm:ss');
          }
          this.dataSource = new MatTableDataSource(this.errorsLogs);

          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          this.cdr.detectChanges();
        } else {
          return;
        }

      }, (err) => {

      }
    )
  }

  changeVisibility(visibleOption, ev) {
    ev.stopPropagation();
    if (visibleOption == 'userNameSearch') {
      this.userNameSearch = !this.userNameSearch;
      this.filterEntity.username = "";
    }
    if (visibleOption == 'activitySearch') {
      this.activitySearch = !this.activitySearch;
      this.filterEntity.activity_name = "";
    }
    if (visibleOption == 'applicationSearch') {
      this.applicationSearch = !this.applicationSearch;
      this.filterEntity.application_platform = "";
    }
    if (visibleOption == 'emailSearch') {
      this.emailSearch = !this.emailSearch;
      this.filterEntity.email = "";
    }
    if (visibleOption == 'errorCodeSearch') {
      this.errorCodeSearch = !this.errorCodeSearch;
      this.filterEntity.error_code = "";
    }
    if (visibleOption == 'errorDesiSearch') {
      this.errorDesiSearch = !this.errorDesiSearch;
      this.filterEntity.error_discription = "";
    }
    if (visibleOption == 'projCodeSearch') {
      this.projCodeSearch = !this.projCodeSearch;
      this.filterEntity.project_code = "";
    }
    if (visibleOption == 'projNameSearch') {
      this.projNameSearch = !this.projNameSearch;
      this.filterEntity.project_name = "";
    }
    if (visibleOption == 'urlSearch') {
      this.urlSearch = !this.urlSearch;
      this.filterEntity.url = "";
    }
    if (visibleOption == 'convertDateSearch') {
      this.convertDateSearch = !this.convertDateSearch;
      this.filterEntity.convertDate = "";
    }
    

  }
  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var errorLogs = this.dataSource.filteredData[i];
      var data = {
        'User Name ': errorLogs.username, 'Activity': errorLogs.activity_name, 'Application Platform': errorLogs.application_platform,
        'Email': errorLogs.email, 'Error Code': errorLogs.error_code, 'Error Description': errorLogs.error_discription,
        'Project Code': errorLogs.project_code, 'Project Name': errorLogs.project_name,  'Created Date': errorLogs.convertDate
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workBook, workSheet, 'SheetName');
    XLSX.writeFile(workBook, 'Error Logs.xlsx');
  }
  pdfExport(){
    var doc = new jsPDF({ // set needed dimensions for any elemen
      
    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var errorLogs = this.dataSource.filteredData[i];
     //this.exportedData.push({name: `name${i}`, description: `description${i}`,
     //statusString: `statusString${i}`});
     var data =[errorLogs.username,errorLogs.activity_name,errorLogs.application_platform, errorLogs.email,errorLogs.error_code,
      errorLogs.error_discription,errorLogs.project_code,errorLogs.project_name,errorLogs.convertDate] ;
     
     this.exportedData.push(data);
   }
   var img = new Image();
   var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
   imgRight.src =srcSparkEV;
   var createdDate = new Date().toLocaleString(('en-GB'));
   img.src = src;
   doc.setFontSize(11);
   doc.setTextColor(100);
   doc.setFontSize(18);
   
    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla',fillColor:[11, 148, 68]},
      styles: {
        cellPadding: 1,
        fontSize: 8,
        
      },
     
      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage:data=>{
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);
        
        doc.text('Error Logs Report' ,doc.internal.pageSize.getWidth() / 2, 11, { align: 'center'});
        
        doc.setTextColor(211,211,211);
        doc.setFontSize(60);
        doc.setGState( doc.GState({opacity: 0.6}));
        doc.text('Confidential/Not for Public use',25,285,null,60, );
        doc.setGState( doc.GState({opacity: 1}));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:'+ createdDate ,15, 18);
        
        doc.text('Generated By:'+ this.localStorage.f_Name ,195, 18,{ align: 'right'});
        doc = this.addWaterMark(doc);
      }
    })
    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow');
    // below line for Download PDF document  
    doc.save('Error Logs report.pdf');
  }
  addWaterMark(doc){
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
        doc.setFontSize(10);
     //Print Page 1 of 4 for example
     doc.text('Page ' +  String(i) + ' of ' + String(newpage),210-20,297-10,null,null,"right");
    return doc;
  }


 
 
}

